<template>
<div>
  <v-menu bottom left>
    <v-toolbar-title slot="activator">
      <v-btn icon>
        <v-icon>more_vert</v-icon>
      </v-btn>
    </v-toolbar-title>

    <v-list>
      <v-list-tile @click="newList">
        <v-list-tile-title>Neu</v-list-tile-title>
      </v-list-tile>
      <v-list-tile @click="openFileDialog = true">
        <v-list-tile-title>
          Öffnen
        </v-list-tile-title>
      </v-list-tile>
      <v-list-tile @click="saveFile">
        <v-list-tile-title>Speichern</v-list-tile-title>
      </v-list-tile>
      <v-list-tile @click="exportXLSX">
        <v-list-tile-title>Als Excel speichern</v-list-tile-title>
      </v-list-tile>
    </v-list>
  </v-menu>

  <v-dialog v-model="openFileDialog" max-width="290">
    <v-card>
      <v-card-title>
        <span class="headline">Datei öffnen</span>
      </v-card-title>
      <v-card-text>
        <v-text-field
          label="Wählen Sie eine Datei..."
          @click='pickFile'
          v-model='fileName'
          prepend-icon='storage'
          >
        </v-text-field>
        <input
          type="file"
          style="display: none"
          ref="fileinput"
          accept="application/json"
          @change="selectFile"
          >
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="openFileDialog = false">Abbrechen</v-btn>
        <v-btn color="blue darken-1" flat @click="openFile">Öffnen</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>

  <v-dialog v-model="newListDialog" max-width="290">
    <v-card>
      <v-card-title>
        <span class="headline">Neue Liste</span>
      </v-card-title>
      <v-card-text>
        Wollen Sie die bestehende Liste tatsächlich löschen?
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="newListDialog = false">Abbrechen</v-btn>
        <v-btn color="blue darken-1" flat @click="clearList">Ja</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</div>
</template>

<script>
import { saveAs } from 'file-saver';
import { Trs, scoreKeys } from '../common';
import makeXLSX from '../xlsx';

function s2ab(s) {
  const buf = new ArrayBuffer(s.length);
  const view = new Uint8Array(buf);
  for (let i = 0; i < s.length; i += 1) {
    // eslint-disable-next-line
    view[i] = s.charCodeAt(i) & 0xFF;
  }
  return buf;
}

function importEntry(entry) {
  const emptyEntry = new Trs();
  entry.a = { ...emptyEntry.a, ...entry.a };
  scoreKeys.forEach((element) => {
    if (Array.isArray(entry.a[element])) {
      const a = [];
      entry.a[element].forEach((arrayElement) => {
        a.push(parseInt(arrayElement, 10));
      });
      entry.a[element] = a;
    } else {
      entry.a[element] = parseInt(entry.a[element], 10);
    }
  });
  entry.a.defaults = {
    medical: false,
    nursing: false,
    therapeutical: false,
  };
  return entry;
}

export default {
  data() {
    return {
      file: null,
      fileName: '',
      openFileDialog: false,
      newListDialog: false,
    };
  },
  methods: {
    pickFile() {
      this.$refs.fileinput.click();
    },
    selectFile(event) {
      const { files } = event.target;
      if (files) {
        this.fileName = files[0].name;
        [this.file] = files;
      }
    },
    openFile() {
      this.openFileDialog = false;
      if (this.file) {
        const reader = new FileReader();
        reader.addEventListener('loadend', () => {
          const dump = JSON.parse(reader.result);
          dump.forEach((fileEntry) => {
            this.$store.commit('push', importEntry(fileEntry));
          });
        });
        this.$store.commit('clear');
        reader.readAsText(this.file);
        this.$router.push({ name: 'list' });
      }
    },
    saveFile() {
      const blob = new Blob(
        [JSON.stringify(this.$store.state.list, null, 2)],
        { type: 'application/json' },
      );
      saveAs(blob, 'trs.json');
    },
    newList() {
      if (this.$store.state.list.length > 0) {
        this.newListDialog = true;
      }
    },
    clearList() {
      this.$store.commit('clear');
      this.newListDialog = false;
      this.$router.push({ name: 'list' });
    },
    exportXLSX() {
      const output = makeXLSX(this.$store.state.list);
      saveAs(
        new Blob(
          [s2ab(output)],
          {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          },
        ),
        'trs.xlsx',
      );
    },
  },
};
</script>
