export const scores = Object.assign(
  {},
  ...Object.values(scoreAreas).map(element => element.components),
);

export const scoreKeys = Object.keys(scores);

export function Trs() {
  this.a = {
    lab: '',
    diagnosis: '',
    date: '',
    dateofbirth: '',
    phase: '',
    defaults: {
      medical: false,
      nursing: false,
      therapeutical: false,
    },
  };
  scoreKeys.forEach((element) => {
    if (clinicalSelects[element].multiple) {
      this.a[element] = [];
    } else {
      this.a[element] = 0;
    }
  });
}

export function cloneTrs(sample) {
  const clone = new Trs();
  clone.a = { ...sample.a };
  clone.a.defaults = { ...sample.a.defaults };
  return clone;
}

export function getSubScore(element, index) {
  let subScore = 0;
  if (Array.isArray(index)) {
    index.forEach((member) => {
      subScore += scores[element][parseInt(member, 10)];
    });
  } else {
    subScore = scores[element][parseInt(index, 10)];
  }
  return subScore;
}

export function getScoreArea(key) {
  let needle = '';
  Object.entries(scoreAreas).forEach(([areaKey, area]) => {
    if (key in area.components) {
      needle = areaKey;
    }
  });
  return needle;
}

export function calculateScore(trs) {
  let score = 100;

  scoreKeys.forEach((element) => {
    const area = getScoreArea(element);
    if (trs.a.defaults[area] === false) {
      score -= getSubScore(element, trs.a[element]);
    }
  });

  Object.keys(trs.a.defaults).forEach((key) => {
    if (trs.a.defaults[key]) {
      const { defaultScore } = scoreAreas[key];
      score -= defaultScore;
    }
  });
  return score;
}

export function itemScore(item, availableScores) {
  if (Array.isArray(item)) {
    const reducer = (accumulator, currentValue) => accumulator + availableScores[currentValue];
    return item.reduce(reducer, 0);
  }
  return availableScores[item];
}
