<template>
<v-layout row wrap>
  <v-flex xs12>
    <v-checkbox
      :label="scoreAreas[area].defaultLabel + '  Die Bearbeitung dieses Bereichs ist damit abgeschlossen.'"
      :color="color"
      v-model="defaultSelected"
      >
    </v-checkbox>
  </v-flex>
</v-layout>
</template>

<script>
import { scoreAreas } from '../common';

export default {
  data() {
    return {
      scoreAreas,
    };
  },
  computed: {
    defaultSelected: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },

  },
  props: {
    area: String,
    value: Boolean,
    color: String,
  },
};
</script>
