<template>
<v-layout column>
  <v-tabs fixed-tabs :slider-color="color" v-model="active">
    <v-tab v-for="(area, key) in areas" :key="key">
      {{ area.name }}
    </v-tab>
    <v-tab-item lazy v-for="(area, key) in areas" :key="key">
      <v-card flat>
        <v-card-text>
          <AreaDefault
            v-if="scoreAreas[key].defaultScore !== null"
            :area="key"
            :value="trs.a.defaults[key]"
            :color="color"
            @input="assignAreaDefault($event, key)"
            />
            <div v-if="trs.a.defaults[key] !== true">
              <ClinicalArea
                :selects="area.selects"
                :scoreArea="scoreAreas[key]"
                :value="trs.a"
                :color="color"
                @input="assign($event)"
                />
            </div>
        </v-card-text>
      </v-card>
    </v-tab-item>
  </v-tabs>
</v-layout>
</template>

<script>
import AreaDefault from '../components/AreaDefault.vue';
import ClinicalArea from '../components/ClinicalArea.vue';
import { clinicalSelects, scoreAreas, cloneTrs } from '../common';
import trsMixin from '../TrsMixin';


function chunkArray(inArray, size) {
  const outArray = [];

  while (inArray.length) {
    outArray.push(inArray.splice(0, size));
  }
  return outArray;
}

function addValues(selects) {
  const valueSelects = [];
  selects.forEach((select) => {
    const valueSelect = {};
    Object.assign(valueSelect, select);
    valueSelect.items = [];
    valueSelect.options.forEach((option, index) => {
      valueSelect.items.push({ text: option, value: index });
    });
    valueSelects.push(valueSelect);
  });
  return valueSelects;
}

const selectsArray = addValues(Object.values(clinicalSelects));
const colors = ['indigo darken-1', 'cyan darken-1', 'orange darken-1'];

export default {
  data() {
    return {
      active: 0,
      scoreAreas,
      areas: {
        medical: {
          name: 'Ärztlicher Bereich',
          selects: chunkArray(
            selectsArray.filter(select => select.group === 'medical'),
            2,
          ),
        },
        nursing: {
          name: 'Pflegerischer Bereich',
          selects: chunkArray(
            selectsArray.filter(select => select.group === 'nursing'),
            2,
          ),
        },
        therapeutical: {
          name: 'Therapeutischer Bereich',
          selects: chunkArray(
            selectsArray.filter(select => select.group === 'therapeutical'),
            2,
          ),
        },
      },
    };
  },
  computed: {
    color() {
      return colors[this.active];
    },
  },
  mixins: [trsMixin],
  props: {
    id: Number,
  },
  methods: {
    assign(event) {
      const newTrs = cloneTrs(this.trs);
      newTrs.a[event.key] = event.value;
      this.trs = newTrs;
    },
    assignAreaDefault(event, areaKey) {
      const newTrs = cloneTrs(this.trs);
      newTrs.a.defaults[areaKey] = event;
      this.trs = newTrs;
    },
  },
  components: {
    AreaDefault,
    ClinicalArea,
  },
};
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
