import { Trs } from './common';

export default {
  computed: {
    trs: {
      get() {
        let { id } = this;
        if (id >= this.$store.state.list.length) {
          const newTrs = new Trs();
          this.$store.commit('push', newTrs);
          id = this.$store.state.list.length - 1;
        }
        this.$store.commit('visit', id);
        return this.$store.state.list[id];
      },
      set(value) {
        this.$store.commit('set', { index: this.id, obj: value });
      },
    },
  },
};
